@import url(https://fonts.googleapis.com/css?family=Carter+One);
html {
  font-size: 62.5%; }

body {
  font-family: Helvetica, Arial, sans-serif;
  font-size: 1.6rem; }

.wrapper {
  margin: 0 auto; }

.header {
  background: skyblue;
  height: 70px; }
  .header h1 {
    color: white;
    text-align: center;
    margin: 0;
    padding-top: 15px;
    font-family: "Carter One", cursive; }

.action_container {
  text-align: center; }
  .action_container .button {
    font-weight: bold;
    font-size: 1.6rem;
    background-color: rgba(145, 255, 0, 0.685);
    margin-top: 20px;
    border-radius: 5px;
    width: 150px;
    outline: none;
    border: none; }

.countryName_container {
  text-align: center; }
  .countryName_container h1 {
    font-size: 40px; }

.box_container {
  display: flex;
  justify-content: center;
  margin: 40px; }
  .box_container .box_left {
    width: 40%;
    margin-right: 50px; }
    .box_container .box_left .flag {
      text-align: center; }
      .box_container .box_left .flag img {
        width: auto;
        height: 100px;
        margin: 0px; }

.leaflet-container {
  height: 350px;
  width: 400px; }

.pictures {
  margin: 0 auto;
  width: 100%;
  height: auto; }
  .pictures img {
    width: 33%;
    height: auto;
    margin: 1px; }

